import "tailwindcss/dist/base.css";
import "styles/globalStyles.css";
import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "Home.js";

import AboutTheClub from "components/hero/TwoColumnWithInput.js";
import AdventCalendar from "components/features/AdventCalendar";
import ChristmaskCalendar from "components/christmask/ChristmaskCalendar";

export default function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route path="/AboutTheClub">
          <AboutTheClub />
        </Route>
        <Route path="/AdventCalendar">
          <AdventCalendar />
        </Route>
        <Route path="/ChristmaskCalendar">
          <ChristmaskCalendar />
        </Route>
      </Switch>
    </Router>
  );
}